import React from 'react'
import { Form } from 'react-bootstrap'


const TitleFormGroup = ({
    className = "",
    style = "",
    errorMessages = [],
    defaultValue=""
}) => {
    return  (       
        <Form.Group controlId="titleForm" className={className} style={{...style}}>
            <Form.Label></Form.Label>
            <Form.Control type="text" name="title" placeholder="" isInvalid={errorMessages.length > 0} defaultValue={defaultValue}/>
            <Form.Control.Feedback type="invalid">
                {errorMessages}
            </Form.Control.Feedback>
        </Form.Group>
    )
}

const DescriptionFormGroup = ({
    className = "",
    style = "",
    errorMessages = [],
    defaultValue=""

}) => {
    return (
        <Form.Group controlId="descriptionForm" className={className} style={{...style}}>
            <Form.Label></Form.Label>
            <Form.Control type="text" name="description" placeholder="" isInvalid={errorMessages.length > 0} defaultValue={defaultValue}/>
            <Form.Control.Feedback type="invalid">
                {errorMessages.find(e=>e)}
            </Form.Control.Feedback>
        </Form.Group>
    )
}

const TalkRoomFormGroups = {}
TalkRoomFormGroups.Title = TitleFormGroup
TalkRoomFormGroups.Description = DescriptionFormGroup

export default TalkRoomFormGroups