import React from 'react'
/**
 * 
 * 
 * topComponent:
 * behindComponent:
 */
const Transparent = ({
    children = [],
    className
}) => {
    return (
        <div className={`transparent ${className}`}>
            {children}
        </div>                
    )
}

Transparent.Front = ({
    children,
    transParent = false
}) => (
    <div className={`front ${transParent ? 'opacity' : ''}`}>
        {children}
    </div>
)

Transparent.Back = ({
    children
}) => (
    <div className='back'>
        {children}
    </div>
)

export default Transparent