import React, { useEffect } from 'react'
import UserPositionModule from '../modules/userPositionModule/UserPositionModule'
import { useSelector, useDispatch } from 'react-redux'

/**
 * 
 * 
 */
const CurrentRoomUserTracer = ({
    talkRoomId
}) => {
    const loginUser = useSelector(state=>state.logStatus.loginUser)
    const dispatch = useDispatch()

    useEffect(() => {
        navigator.geolocation.getCurrentPosition(position=> {
            dispatch(UserPositionModule.actions.changeCurrentUserPosition({
                talkRoomId,
                userId: loginUser.id,
                position : {
                    latitude : position.coords.latitude,
                    longitude : position.coords.longitude
                }
            }))
        }, err => {
            console.log(`code:${err.code} ${err.message}`)
        })
        navigator.geolocation.watchPosition(position => {
            dispatch(UserPositionModule.actions.changeCurrentUserPosition({
                talkRoomId,
                userId: loginUser.id,
                position : {
                    latitude : position.coords.latitude,
                    longitude : position.coords.longitude
                }
            }))
        }, err => {
            console.log(err)
        })
    }, [])

    return <></>
}

export default CurrentRoomUserTracer